<template>
 <div data-app class="col-xs-12 col-md-12">
   <div class="table-responsive">
    <div style="display:flex;flex-direction:row;margin-bottom:0.4em">
                <div style="display:flex;flex-direction:row;">
                <label style='padding-top:0.2em;margin-right:0.5em'>Department:</label>
                <select v-model="dept" class="form-control select" style='width:15em' @change="new_list">
                    <option v-for="subdept in subdept_list ">{{ subdept }}</option>
                </select>
                </div>
                <div style="display:flex;flex-direction:row;margin-left:auto">
                <label style='padding-top:0.2em;margin-right:0.5em'>Status:</label>
                <select v-model="stat" class="form-control select" style='width:15em' @change="new_list">
                    <option v-for="c_stat in stat_list">{{ c_stat }}</option>
                </select>
                </div>
                </div>
    <v-data-table
      :headers="headers"
      :items="pooldata"
      class="elevation-1"
      :rows-per-page-items="[20]"
    >
      <template v-slot:items="props">
      <td><a href="" :id= props.item.id v-on:click.prevent="editform($event)">Edit</a></td>
      <td style='width:5em'>{{ props.item.id }}</td>
      <td>{{ props.item.req_name }}</td>
      <td>{{ props.item.item }}</td>
      <td style='width:55em'>{{ props.item.description }}</td>
      <td>{{ props.item.location }}</td>
      <td>{{ props.item.status }}</td>
      <td>{{ props.item.remarks }}</td>
    
      <template v-slot:pageText="props">
        Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
      </template>
    
    </template>
    </v-data-table>
</div>
</div>
</template>

<script>
import axios from 'axios';

export default {
    data(){
        return{
           search: '',
            pooldata : [],
            userid:"",
            dept:'ITSD',
            stat:'Not Started',
            subdept_list:['ITSD','Resources'],
            stat_list:['Not Started','In Progress','Completed'],
        headers: [
          { text: 'Action',value :'id',sortable: false},
          { text: 'ID',value :'id',sortable: false},
          { text: 'Requester',value :'req_name',sortable: false},
          { text: 'Item Category',value :'item',sortable: false},
          { text: 'Description',value:'description',sortable: false},
          { text: 'Location',value:'location',sortable: false},
          { text: 'Status',value:'status',sortable: false},
          { text: 'Remarks',value:'remarks',sortable: false},
        ],
        }        
    },
    created:function(){
        var formData = new FormData();
        formData.append('subdept',this.dept);
        formData.append('stat',this.stat);
        axios.post('RequestPortal/backend/checklist_fetch.php',formData)
        .then(
            (response)=>{
                this.pooldata = response.data;
                this.pooldata.id = response.data.id;
                this.pooldata.req_name = response.data.req_name;
                this.pooldata.item = response.data.item;
                this.pooldata.description = response.data.description;
                this.pooldata.location = response.data.location;
                this.pooldata.status = response.data.status;
                this.pooldata.remarks = response.data.remarks;
            }
        );       
    },
    methods:{
        editform(event){
            var targetID = event.currentTarget.id;
            document.getElementById('tmp_id').value = targetID;
            this.$router.push('edit_checklist');
        },
        new_list(event){
        var formData = new FormData();
        formData.append('subdept',this.dept);
        formData.append('stat',this.stat);
        axios.post('RequestPortal/backend/checklist_fetch.php',formData)
        .then(
            (response)=>{
                this.pooldata = response.data;
                this.pooldata.id = response.data.id;
                this.pooldata.req_name = response.data.req_name;
                this.pooldata.item = response.data.item;
                this.pooldata.description = response.data.description;
                this.pooldata.location = response.data.location;
                this.pooldata.status = response.data.status;
                this.pooldata.remarks = response.data.remarks;
            }
        )
    }
}
}
</script>


